				<!-- <template #tabName1>{{$t('StoreRanking.sale')}}</template>
				<template #tabName2>{{$t('StoreRanking.achievement')}}</template> -->
			</header-navigation>
		</view>
		<!-- 头部 end -->
		<!-- tab -->
		<view class="disp-flex shoppingRanking-tab" v-if="Object.keys(Language)!=''">
			<view @click="shoppingRankingTab(0)" :class="tabIndex==0?'shoppingRanking-tab-itemActive':''"
				class="shoppingRanking-tab-item">{{Language.tab_rank_sales.name}}</view>
			<!-- <view @click="shoppingRankingTab(1)" :class="tabIndex==1?'shoppingRanking-tab-itemActive':''"
				class="shoppingRanking-tab-item">{{$t('StoreRanking.achievement')}}</view> -->
