				class="shoppingRanking-tab-item">AVT</view>
			<view @click="shoppingRankingTab(3)" :class="tabIndex==3?'shoppingRanking-tab-itemActive':''"
				class="shoppingRanking-tab-item">UPT</view>
		</view>
		<!-- tab end -->
		<!-- 列表 -->
		<view class="list-box" v-if="dataList.length>0">
			<view class="list" v-for="(item,index) in dataList" :key="index">
				<view class="list-top disp-flex align-items">
					<view v-if="index>2" class="top-icon font">{{index+1}}</view>
					<image v-if="index==0" src="../../static/ICN_No.1@2x.png" class="top-iconImg"></image>
					<image v-if="index==1" src="../../static/ICN_No.2@2x.png" class="top-iconImg"></image>
					<image v-if="index==2" src="../../static/ICN_No.3@2x.png" class="top-iconImg"></image>
					<view>
						<view class="top-title disp-flex align-items">
							<view class="list-title ellipsis">{{item.staff_name}}</view>
						</view>
						<view class="disp-flex align-items">
							<view class="progress">
								<view :style="{width:item.sales_rate+'%'}" class="progress-bar"></view>
							</view>
							<view class="progress-num">{{item.sales_rate}}%</view>
						</view>
					</view>
				</view>
				<view class="list-border"></view>
				<!-- <view class="list-bottom disp-flex disp-justifyAround">
					<view class="list-bottom-li">
						<view class="list-bottom-name">Net Revenue</view>
						<view class="list-bottom-num font">{{item.Net_Revenue}}</view>
					</view>
