							<view class="comm-bottom-num font">{{item.upt}}</view>
						</view>
					</view>
					<view class="comm-bottom-item2">
						<view class="comm-bottom-item2-marg disp-flex align-items">
							<view class="comm-bottom-name color9">Trans</view>
							<view class="comm-bottom-num font">{{item.trans}}</view>
						</view>
						<view class="disp-flex align-items">
							<view class="comm-bottom-name color9">Units</view>
							<view class="comm-bottom-num font">{{item.units}}</view>
