			</view>
		</view>
		<!-- 列表 end -->
		<!-- 暂无数据 -->
		<view class="empty" v-else>暂无数据</view>
		<!-- 查看更多 -->
		<!-- <uni-load-more :status="status" :content-text="contentText"></uni-load-more> -->
	</view>
</template>

<script>
	const APP = getApp();
	import headerNavigation from "@/components/header/header.vue";
	import {
		formatTime,
		getLanguageData
	} from "@/utils/utils.js";
	import {
		getSalesRanking,
		getPerformanceRanking
	} from "@/utils/http.js";
	export default {
		components: {
			headerNavigation
		},
		data() {
			return {
				tabIndex: 0, //标签页销售排行，业绩达成排行tab下标
				menuIndex: 0, //当前菜单下标，本日，...
				selectData: '', //选择时间的当前文字
				status: "more", //more:加载前，loading：加载中，noMore：没有数据了
				contentText: {
					contentdown: '查看更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多'
				},
				timeType: "D",
				timeValue: "",
				dataList: [], //销售排行数据
				org0: "",
				org1: "",
				org2: "",
				org3: "",
				org4: "",
				Language:{}
			}
		},
		onLoad() {
			this.$i18n.locale = APP.globalData.code;
			this.selectData = this.$t('Other-months');
			getLanguageData().then((res)=>{
				this.Language=res;
			})
		},
		onReachBottom() {
			// console.log("分页")
		},
		methods: {
			// 当前选中的菜单
			menuClick(data) {
				this.menuIndex = data.menuIndex;
				this.timeValue = data.timeValue || formatTime(new Date);
				this.timeType = data.calendarData || "D";
				this.org0 = data.lvl0Select;
				this.org1 = data.lvl1Select;
				this.org2 = data.lvl2Select;
				this.org3 = data.lvl3Select;
				this.org4 = data.lvl4Select;
				if (this.tabIndex === 0) { //销售排行
					this.hotGoodsList = [];
					this.salesRanking("n");
				} else if (this.tabIndex === 1) { //业绩达成排行
					this.salesRanking("r");
				} else if (this.tabIndex === 2) { //net
					this.salesRanking("v");
				} else { //vat
					this.salesRanking("u");
				}
			},
			shoppingRankingTab(e) {
				this.tabIndex = e;
				if (this.tabIndex === 0) { //销售排行
					this.hotGoodsList = [];
					this.salesRanking("n");
				} else if (this.tabIndex === 1) { //业绩达成排行
					this.salesRanking("r");
				} else if (this.tabIndex === 2) { //net
					this.salesRanking("v");
				} else { //vat
					this.salesRanking("u");
				}
			},
			// 销售排行
			salesRanking(orderBy) {
				let that = this;
				that.$showLoading();
				getSalesRanking({
					timeType: that.timeType,
					timeValue: that.timeValue,
					org0: that.org0,
					org1: that.org1,
					org2: that.org2,
					org3: that.org3,
					org4: that.org4,
					orderBy: orderBy
				}).then((res) => {
					uni.hideLoading();
					if (res == undefined || res == null || res == '') {
						that.dataList = [];
					} else {
						that.dataList = res;
					}
				})
			},
			// 业绩达成排行
			performanceRanking() {
				let that = this;
				that.$showLoading();
				getPerformanceRanking({
					timeType: that.timeType,
					timeValue: that.timeValue,
					org0: that.org0,
					org1: that.org1,
					org2: that.org2,
					org3: that.org3,
					org4: that.org4,
				}).then((res) => {
					uni.hideLoading();
					if (res == undefined || res == null || res == '') {
						that.dataList = [];
					} else {
						that.dataList = res;
					}
				})
			}
		}
	}
</script>

